import React, { useEffect, useState } from "react";
import "./App.css";
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";
import history from "./history";
import Home from "./Home";
import Perfil from "./perfil";
import News from "./news";
import Registros from "./registros";
import "semantic-ui-css/semantic.min.css";
import userData from "./employee.json";
import {
  Container,
  Header,
  Image,
  Menu,
  Icon,
  Modal,
  Button,
  Input,
  Segment,
  Dropdown,
} from "semantic-ui-react";
import Logo from "./images/logo.png";

function App() {
  let iniciado = null;
  let caption = "Sign In";
  if (typeof sessionStorage.user !== "undefined") {
    iniciado = sessionStorage.user;
    caption = "Sign Out";
  }

  const [user, setUser] = useState(iniciado);
  const [session, setSession] = useState(caption);
  const [open, setOpen] = React.useState(false);
  const [width, setWidth] = useState(window.innerWidth);
  const [height, setHeight] = useState(window.innerHeight);
  const [password, setPassword] = useState("");
  const [activeItem, setActiveItem] = useState("cultura");

  const handleItemClick = (e, { name }) => {
    setActiveItem(name);
  };
  const onChangeHandler = (event) => {
    const { name, value } = event.currentTarget;

    if (name === "userPassword") {
      setPassword(value);
    }
  };

  const login = (event, password) => {
    event.preventDefault();
    var found = false;
    for (var i = 0; i < userData.length; i++) {
      if (userData[i].id === password) {
        found = true;
        setUser(userData[i].Nombre);
        sessionStorage.user = userData[i].Nombre;
        setSession("Sign Out");
        break;
      }
    }
    if (!found) alert(`Usuario No encontrado`);
  };

  return (
    <Router history={history}>
      <Container style={{ marginTop: "3em" }}>
        <Header as="h1" dividing style={{ color: "red" }}>
          <Image avatar src={Logo} />
          SASSO
        </Header>
        <Menu inverted color="red" pointing secondary size="mini">
          <Menu.Item
            name="cultura"
            active={activeItem === "cultura"}
            onClick={handleItemClick}
            color="red"
            as={Link}
            to="/"
          >
            CULTURA
          </Menu.Item>
          <Dropdown item text="INCIDENTES">
            <Dropdown.Menu>
              <Dropdown.Item
                icon="feed"
                text="Recientes"
                as={Link}
                to="/registros_recientes"
              />
              <Dropdown.Item
                icon="edit"
                text="Registrar"
                as={Link}
                to="/registros"
              />
            </Dropdown.Menu>
          </Dropdown>
          <Menu.Item
            name="estadistica"
            active={activeItem === "estadistica"}
            onClick={handleItemClick}
            as={Link}
            to="/perfil"
          >
            PERFIL
          </Menu.Item>
          <Menu.Menu position="right">
            <Modal
              basic
              onClose={() => {
                setOpen(false);
              }}
              onOpen={() => setOpen(true)}
              open={open}
              size="mini"
              trigger={
                <Menu.Item
                  name={session}
                  active={activeItem === { session }}
                  onClick={handleItemClick}
                />
              }
            >
              {!user ? (
                <Segment textAlign="center">
                  <Header icon>
                    <Icon name="key" />
                    Sign In
                  </Header>
                  <Header as="h3">
                    <Modal.Content>
                      <Input iconPosition="left" placeholder="Password">
                        <Icon name="key" />
                        <input
                          name="userPassword"
                          value={password}
                          onChange={(event) => onChangeHandler(event)}
                        />
                      </Input>
                    </Modal.Content>
                  </Header>
                  <Modal.Actions>
                    <Button color="red" inverted onClick={() => setOpen(false)}>
                      <Icon name="remove" /> Cancel
                    </Button>
                    <Button
                      color="green"
                      inverted
                      onClick={(event) => {
                        login(event, password);
                        setOpen(false);
                      }}
                    >
                      <Icon name="checkmark" /> Sign In
                    </Button>
                  </Modal.Actions>
                </Segment>
              ) : (
                <Segment textAlign="center">
                  <Header icon>
                    <Icon name="key" />
                    Sign Out
                  </Header>
                  <Modal.Actions>
                    <Button color="red" inverted onClick={() => setOpen(false)}>
                      <Icon name="remove" /> Cancel
                    </Button>
                    <Button
                      color="green"
                      inverted
                      onClick={() => {
                        setUser("");
                        sessionStorage.removeItem("user");
                        iniciado = null;
                        setSession("Sign In");
                        setOpen(false);
                      }}
                    >
                      <Icon name="checkmark" /> OK
                    </Button>
                  </Modal.Actions>
                </Segment>
              )}
            </Modal>
          </Menu.Menu>
        </Menu>
        {user ? (
          <header
            style={{
              fontSize: "18px",
            }}
          >
            <strong>Bienvenido {user}</strong>
          </header>
        ) : null}
        <br />
        <div className="vscrolling-wrapper" style={{ height: height * 0.8 }}>
          <Switch>
            <Route
              exact
              path="/registros_recientes"
              render={(props) => <Home {...props} user={user} />}
            />
            <Route
              path="/registros"
              render={(props) => <Registros {...props} user={user} />}
            />
            <Route
              path="/perfil"
              render={(props) => <Perfil {...props} user={user} />}
            />
            <Route
              path="/"
              render={(props) => <News {...props} user={user} />}
            />
          </Switch>
        </div>
      </Container>
    </Router>
  );
}

export default App;
