import React, { useEffect, useState } from "react";
import { Container, Segment, Button, Feed, Icon } from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";

let datas = [];

class Tabla {
  constructor(
    fecha,
    fecha_hr,
    nombre,
    area,
    cat,
    rubro,
    obs,
    pac,
    fecha_pac,
    resp,
    src
  ) {
    this.fecha = fecha;
    this.fecha_hr = fecha_hr;
    this.nombre = nombre;
    this.area = area;
    this.cat = cat;
    this.rubro = rubro;
    this.obs = obs;
    this.pac = pac;
    this.fecha_pac = fecha_pac;
    this.resp = resp;
    this.src = src;
  }
}

function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const fechaA = a.fecha;
  const fechaB = b.fecha;

  let comparison = 0;
  if (fechaA > fechaB) {
    comparison = -1;
  } else if (fechaA < fechaB) {
    comparison = 1;
  }
  return comparison;
}

function dateFormat(num) {
  if (num) {
    const fechaxl = new Date((num - (25567 + 1)) * 86400 * 1000);
    let xldd = fechaxl.getDate();
    let xlmm = fechaxl.getMonth() + 1;
    let xlyyyy = fechaxl.getFullYear();
    if (xldd < 10) xldd = "0" + xldd;
    if (xlmm < 10) xlmm = "0" + xlmm;
    let xldate = xlyyyy + "-" + xlmm + "-" + xldd;
    return xldate;
  }
}

function Home({ user }) {
  const [conteo, setConteo] = useState(0);
  const [cartas, setCartas] = useState("");
  const areas = [
    { id: 1, name: "Area de Reciclaje", short: "AR" },
    { id: 2, name: "Linea1", short: "L1" },
    { id: 3, name: "Linea2", short: "L2" },
    { id: 4, name: "Linea3", short: "L3" },
    { id: 5, name: "Linea4", short: "L4" },
    { id: 6, name: "Linea5", short: "L5" },
    { id: 7, name: "PTAR", short: "PT" },
    { id: 8, name: "Sala de Jarabe", short: "SJ" },
    { id: 9, name: "Taller de Mtto", short: "TM" },
    { id: 9, name: "Area de Operaciones", short: "OP" },
  ];

  const sacardata = async () => {
    datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[0];
    const rows = await sheet.getRows();
    let lr = rows.length + 1;
    var j;
    await sheet.loadCells("A1:K" + lr.toString());
    for (j = 1; j < lr; j++) {
      const ar = sheet.getCell(j, 0);
      const br = sheet.getCell(j, 1);
      const cr = sheet.getCell(j, 2);
      const dr = sheet.getCell(j, 3);
      const er = sheet.getCell(j, 4);
      const fr = sheet.getCell(j, 5);
      const gr = sheet.getCell(j, 6);
      const hr = sheet.getCell(j, 7);
      const ir = sheet.getCell(j, 8);
      const jr = sheet.getCell(j, 9);
      const kr = sheet.getCell(j, 10);
      datas.push(
        new Tabla(
          dateFormat(ar.value),
          dateFormat(br.value),
          cr.value,
          dr.value,
          er.value,
          fr.value,
          gr.value,
          hr.value,
          dateFormat(ir.value),
          jr.value,
          kr.value
        )
      );
    }
    console.log(datas);
    setCartas(
      datas
        .sort(compare)
        .slice(0, 15)
        .map((item, i) => {
          return (
            <Feed.Event key={i}>
              <Feed.Label image={item.src}></Feed.Label>
              <Feed.Content>
                <Feed.Date>{item.fecha}</Feed.Date>
                <Feed.Summary>
                  Hallazgo de {item.cat} en {item.area}
                </Feed.Summary>
                <Feed.Extra text>{item.rubro}</Feed.Extra>
                <Feed.Extra text>{item.obs}</Feed.Extra>
                <Feed.Meta>
                  <Feed.Like>
                    <Icon name="user" />
                    {item.resp}
                  </Feed.Like>
                </Feed.Meta>
              </Feed.Content>
            </Feed.Event>
          );
        })
    );
  };

  useEffect(() => {
    if (conteo === 0) {
      if (!cartas) {
        sacardata();
      }
      setConteo(1);
    }
  }, [conteo, cartas]);

  const iniciar = () => {
    var priceEls = document.querySelectorAll(".item");
    for (var i = 0; i < priceEls.length; i++) {
      var ele = priceEls[i].innerText;
      console.log(ele);
      if (ele === "Sign In") {
        priceEls[i].click();
        break;
      }
    }
  };

  return (
    <Container>
      <Segment>
        <h1>Registros Recientes</h1>
        {user ? (
          <Feed>{cartas}</Feed>
        ) : (
          <Button
            color="red"
            inverted
            fluid
            icon="key"
            content="Sign In"
            onClick={() => {
              iniciar();
            }}
          />
        )}
      </Segment>
    </Container>
  );
}

export default Home;
